<template>
  <div>
    <!-- Header 区域 -->
    <van-nav-bar
      fixed
      title="文章详情"
      left-arrow
      @click-left="$router.back()"
    />

    <!-- 文章-加载中... -->
    <van-loading color="#1989fa" v-if="Object.keys(artObj).length === 0"
      >疯狂加载中...</van-loading
    >

    <div v-else>
      <!-- 文章信息区域 -->
      <div class="article-container">
        <!-- 文章标题 -->
        <h1 class="art-title">{{ artObj.title }}</h1>

        <!-- 用户信息 -->
        <van-cell
          center
          :title="artObj.aut_name"
          :label="formatDate(artObj.pubdate)"
        >
          <template #icon>
            <img :src="artObj.aut_photo" alt="" class="avatar" />
          </template>
          <template #default>
            <div>
              <van-button
                type="info"
                size="mini"
                v-if="artObj.is_followed"
                @click="followedFn(true)"
                >已关注</van-button
              >
              <van-button
                icon="plus"
                type="info"
                size="mini"
                plain
                @click="followedFn(false)"
                v-else
                >关注</van-button
              >
            </div>
          </template>
        </van-cell>

        <!-- 分割线 -->
        <van-divider></van-divider>

        <!-- 文章内容 -->
        <div class="art-content" v-html="artObj.content"></div>

        <!-- 分割线 -->
        <van-divider>End</van-divider>

        <!-- 点赞 -->
        <div class="like-box">
          <van-button
            icon="good-job"
            type="danger"
            size="small"
            v-if="artObj.attitude === 1"
            @click="loveFn(true)"
            >已点赞</van-button
          >
          <van-button
            icon="good-job-o"
            type="danger"
            plain
            size="small"
            v-else
            @click="loveFn(false)"
            >点赞</van-button
          >
        </div>
      </div>

      <!-- 评论区域 -->
      <CommentList />
    </div>
  </div>
</template>

<script>
import {
  detailAPI,
  followedAPI,
  unFollowedAPI,
  loveArticleAPI,
  unLoveArticleAPI,
} from "@/api";
import { timeAgo } from "@/utils/date";
import CommentList from "./CommentList.vue";

export default {
  name: "ArticleDetail",
  data() {
    return {
      artObj: {}, //存放当前文章详情
    };
  },
  components: {
    CommentList,
  },
  async created() {
    //创建时获取文章详情
    const res = await detailAPI(this.$route.query.art_id);
    this.artObj = res.data.data;
  },
  methods: {
    formatDate: timeAgo,

    //关注按钮点击事件
    async followedFn(bool) {
      if (bool) {
        //点击的是已关注
        //进行取消关注操作
        this.artObj.is_followed = false;
        const res = await unFollowedAPI(this.artObj.aut_id);
      } else {
        //点击的是关注
        // 进行关注操作
        this.artObj.is_followed = true;
        const res = await followedAPI(this.artObj.aut_id);
      }
    },

    //点赞按钮点赞事件
    async loveFn(bool) {
      if (bool) {
        //点击的是已点赞
        //进行取消点赞操作
        this.artObj.attitude = 0;
        const res = await unLoveArticleAPI(this.artObj.art_id);
        // console.log(res);
      } else {
        //点击的是点赞
        // 进行点赞操作
        this.artObj.attitude = 1;
        const res = await loveArticleAPI(this.artObj.art_id);
        // console.log(res);
      }
    },
  },
};
</script>

<style scoped lang="less">
.article-container {
  padding: 10px;
  margin-top: 46px;
}
.art-title {
  font-size: 16px;
  font-weight: bold;
  margin: 10px 0;
}

.art-content {
  font-size: 12px;
  line-height: 24px;
  width: 100%;
  overflow-x: scroll;
  word-break: break-all;
  /deep/ img {
    width: 100%;
  }
  /deep/ pre {
    white-space: pre-wrap;
    word-wrap: break-word;
  }
}

.van-cell {
  padding: 5px 0;
  &::after {
    display: none;
  }
}

.avatar {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #f8f8f8;
  margin-right: 5px;
  border: none;
}

.like-box {
  display: flex;
  justify-content: center;
}

.van-loading {
  padding-top: 46px;
  text-align: center;
}
</style>
